import React from 'react';
import { useTranslation } from 'react-i18next';
import { Icon } from '@/components/Icon';
import { CenterIcon, SubText, TextHeader, Wrapper } from './Toolbox.styles';

type Props = {
  isFirst?: boolean;
  handleOpenToolbox?: () => void;
};

const AddToolbox = ({ isFirst = true, handleOpenToolbox }: Props) => {
  const { t } = useTranslation();
  return (
    <Wrapper
      isFirst={isFirst}
      onClick={handleOpenToolbox}
      data-testid="toolboxwrapper"
    >
      <CenterIcon id={isFirst ? '32' : '16'}>
        <Icon
          name="plus_icon"
          color="--icon-card-neutral-default"
          size={isFirst ? 32 : 16}
        />
      </CenterIcon>
      {isFirst && <TextHeader>{t('noumena.chambers.header')}</TextHeader>}
      {isFirst && <SubText>{t('noumena.chambers.subtext')}</SubText>}
    </Wrapper>
  );
};

export default AddToolbox;
