import { type FC, useCallback, useEffect, useRef, useState } from 'react';
import {
  DragDropContext,
  type DropResult,
  type PreDragActions,
  type SensorAPI,
  type SnapDragActions,
} from 'react-beautiful-dnd';
import {
  type ElementOutput,
  type ElementPositionInput,
  type NoumCustomPreviewElementInput,
  SpaceTypeEnum,
} from '@/apollo/generated/types';
import { reorderList } from '@/utils/list';
import { ElementUtils } from '@/utils/element';
import { useEditChamberState } from '@/screens/Chamber/EditChamber/provider';
import { useUpdateElementPositionHelper } from '@/features/noums/hooks/spaceQuery';
import { type RearrageProps } from './types';
import { DroppableArea } from './DroppableArea';

export const Rearrage: FC<RearrageProps> = ({
  isCustomPreview,
  selectedCustomPreviewTab,
}) => {
  const {
    id: spaceId,
    space,
    elements: originalEls,
    sendRerenderEvent,
    updateCustomPreviewElements,
    customPreviewElements,
    hasUnsavedCustomPreview,
  } = useEditChamberState();

  const sensorAPIRef = useRef<SensorAPI | null>(null);
  const [isDragging, setIsDragging] = useState(false);
  const [elements, setElements] = useState<ElementOutput[]>([]);
  const [positionChanged, setIsPositionChanged] = useState(false);
  const { updateElementPositionHelper } = useUpdateElementPositionHelper();

  const useSensor = (api: SensorAPI) => {
    sensorAPIRef.current = api;
  };

  const lift = useCallback(
    (draggableId: string): SnapDragActions | null => {
      setIsPositionChanged(true);
      if (isDragging) {
        return null;
      }
      const api = sensorAPIRef.current;
      if (!api) {
        return null;
      }

      const preDrag: PreDragActions | null = api.tryGetLock(
        draggableId,
        () => {},
      );

      if (!preDrag) {
        return null;
      }

      return preDrag.snapLift();
    },
    [isDragging],
  );

  const onDragStart = useCallback(() => {
    setIsDragging(true);
  }, []);

  const onDragEnd = useCallback(
    (result: DropResult) => {
      setIsDragging(false);

      if (
        !spaceId ||
        !result.destination ||
        result.destination.index === result.source.index
      ) {
        return;
      }

      const newElements = reorderList<ElementOutput>(
        elements,
        result.source.index,
        result.destination.index,
      );
      if (isCustomPreview && updateCustomPreviewElements) {
        const elementsCP = newElements.map((el, index) => ({
          _id: el._id,
          customPosition: index + 1,
        })) as NoumCustomPreviewElementInput[];
        updateCustomPreviewElements(elementsCP);
      }
      setIsPositionChanged(true);
      setElements(newElements);
      sendRerenderEvent();
    },
    [
      spaceId,
      elements,
      isCustomPreview,
      sendRerenderEvent,
      updateCustomPreviewElements,
    ],
  );

  useEffect(() => {
    if (positionChanged && ElementUtils.isElementPositionChanged1(elements)) {
      if (isCustomPreview) return;
      const input = elements.map((el, index) => ({
        _id: el._id,
        position: index + 1,
      })) as ElementPositionInput[];
      updateElementPositionHelper(spaceId, input);
    }
  }, [
    elements,
    isCustomPreview,
    positionChanged,
    spaceId,
    updateCustomPreviewElements,
    updateElementPositionHelper,
  ]);

  useEffect(() => {
    let updateElements;
    if (hasUnsavedCustomPreview) {
      updateElements = ElementUtils.unSavedfilterAndSortCPPublished(
        originalEls,
        customPreviewElements,
        selectedCustomPreviewTab,
      );
    } else if (isCustomPreview) {
      updateElements = ElementUtils.filterAndSortCPPublished(
        originalEls,
        selectedCustomPreviewTab,
      );
    } else {
      updateElements = originalEls;
    }
    if (updateElements) {
      const newElements = ElementUtils.filterMasterElement(
        updateElements,
        space?.type === SpaceTypeEnum.Home,
      );
      setElements(newElements);
    }
  }, [
    customPreviewElements,
    hasUnsavedCustomPreview,
    isCustomPreview,
    originalEls,
    selectedCustomPreviewTab,
    space?.type,
  ]);

  const handleCPVisibilityChange = useCallback(
    (elementId: string, isVisible: boolean) => {
      updateCustomPreviewElements?.([
        { _id: elementId, isCustomPreviewVisible: isVisible },
      ]);
    },
    [updateCustomPreviewElements],
  );

  return (
    <DragDropContext
      onDragStart={onDragStart}
      onDragEnd={onDragEnd}
      sensors={[useSensor]}
    >
      <DroppableArea
        spaceId={spaceId}
        elements={elements}
        lift={lift}
        isHomeNoum={space?.type === SpaceTypeEnum.Home}
        isCustomPreview={isCustomPreview}
        handleCPVisibilityChange={handleCPVisibilityChange}
        selectedCustomPreviewTab={selectedCustomPreviewTab}
      />
    </DragDropContext>
  );
};

export default Rearrage;
