import { useMemo, useCallback, useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router';
import { t } from 'i18next';
import {
  useGetSpaceByIdQuery,
  useMarkSpaceAsEditedMutation,
  useUnlinkNoumsMutation,
} from '@/apollo/graphql';
import {
  SpaceStatusEnum,
  NoumLayoutStatusFilter,
  NoumLayoutStatus,
} from '@/apollo/generated/types';
import { SpaceUtils } from '@/utils/space';
import { getTimeStampForDisplaying } from '@/utils/getTimeStampForDisplaying';
import { type TNoumEditModal } from '@/screens/Chamber/components/modals/NoumEditOptionsModal/types';
import ROUTES from '@/constants/routes';
import { useThemeContext } from '@/providers/ThemeProvider';
import { type DropdownValueType } from '@/components/Dropdown';
import { DefaultFonts } from '@/screens/Chamber/components/ThemePanel/constants';
import { useAuth } from '@/features/auth/contexts';
import { useLaunchDarkly } from '@/hooks/launchDarkly';
import { useToast } from '@/hooks/toast';
import { useConnections } from './useConnections';
import { useCampaigns } from './campaigns/useCampaigns';
import { useDiscardSpaceChangeHelper } from '../spaceQuery';
import { useChangeProjectChamberStatusHelper } from './projectNoum/useChangeProjectChamberStatusHelper';

export const useNoumEdit = (id: string) => {
  const navigate = useNavigate();
  const { user } = useAuth();
  const {
    flags: { noumEditor2 },
  } = useLaunchDarkly();
  const [isVisited, setIsVisited] = useState<boolean>(false);
  const [isOpen, toggle] = useState(false);
  const [isOnLoad, setIsOnLoad] = useState(false);
  const [isRestored, setIsRestored] = useState(false);
  const [openArchive, setOpenArchive] = useState(false);
  const [openInvites, setOpenInvites] = useState(false);
  const [openPermission, setOpenPermission] = useState(false);
  const [openNoumAds, setOpenNoumAds] = useState(false);
  const [openBroadcast, setOpenBroadcast] = useState(false);
  const [openThemePanel, setOpenThemePanel] = useState(false);
  const [openManageMembers, setOpenManageMembers] = useState(false);
  const [openNoumenaCopilot, setOpenNoumenaCopilot] = useState(false);
  const [openRestoreModal, setOpenRestoreModal] = useState(false);
  const [sectionStatus, setsectionStatus] = useState<NoumLayoutStatus>();
  const { addToast } = useToast();
  const { setSelectedThemeId, isSettingTheme, setSelectedFonts } =
    useThemeContext();

  const {
    data,
    refetch: refetchSpaceById,
    loading,
  } = useGetSpaceByIdQuery({
    variables: {
      id,
      status: NoumLayoutStatusFilter.Unpublished,
      editorV2Enabled: noumEditor2,
    },
    skip: !id,
    fetchPolicy: 'cache-and-network',
  });

  const location = useLocation();

  const {
    loading: loadingConnections,
    connections,
    infiniteState: connectionsInfiniteState,
    fetchMoreConnections,
    refetchConnections,
    approvedConnectionsLoading,
    appConnectionsData,
    approvedConnectionsError,
    approvedInfiniteState,
    fetchMoreApprovedConnections,
    approvedConnectionsRefetch,
    invitedConnections,
    invitedConnectionsError,
    invitedConnectionsLoading,
    invitedInfiniteState,
    refetchInvitedConnections,
    fetchMoreInvitedConnections,
  } = useConnections(id, true, 10);

  const {
    loading: campaignsLoading,
    campaigns,
    infiniteState,
    fetchMoreCampaigns,
    refetchCampaigns,
  } = useCampaigns(id, true, 10);

  const [markSpaceAsEdited, { loading: markSpaceAsEditedLoading }] =
    useMarkSpaceAsEditedMutation();

  const space = data?.getSpaceById ?? undefined;
  const isOwner = space?.uid?._id === user?._id;
  const links = space?.link;
  const linkedNoumsCount = links?.linkedNoumsCount;
  const isArchived = space?.status === SpaceStatusEnum.Archived;

  const linkedNoumIds = useMemo(() => {
    const linkedIds: string[] = [];
    links?.linkedNoums.forEach((link) => {
      linkedIds.push(link?._id!);
    });
    return linkedIds;
  }, [links]);

  // TODO: Consult this part of code
  // useEffect(() => {
  //   if (!loading && isOnLoad && !data) {
  //     navigate('/noums');
  //   }
  // }, [data, loading, isOnLoad, navigate]);

  useEffect(() => {
    const isPostPage = location?.pathname.indexOf('/post') > -1;
    if ((!isPostPage && space && !isOwner) || isArchived) {
      const route = space?._id
        ? `/noum/${space?._id}${location.search}${location.hash}`
        : ROUTES.NOUMS;
      if (route !== `${location.pathname}${location.search}${location.hash}`)
        navigate(route, { replace: true });
    }
  }, [
    isOwner,
    isArchived,
    navigate,
    space,
    location.pathname,
    location.search,
    location.hash,
  ]);

  useEffect(() => {
    if (space && space.theme?._id) {
      setSelectedThemeId(SpaceUtils.getAppliedTheme(space));
    } else {
      setSelectedThemeId(undefined);
    }
    const spaceFonts = SpaceUtils.getAppliedFont(space);

    if (
      spaceFonts &&
      typeof spaceFonts === 'object' &&
      Object.keys(spaceFonts).length > 0
    ) {
      setSelectedFonts(spaceFonts);
    } else {
      setSelectedFonts(DefaultFonts);
    }
  }, [setSelectedFonts, setSelectedThemeId, space]);

  const hasUnsaved = useMemo(
    () =>
      SpaceUtils.hasUnsavedElement(space, noumEditor2) ||
      SpaceUtils.hasUnsavedSetting(space),
    [noumEditor2, space],
  );

  const hasUnsavedNoumLayout = useMemo(
    () =>
      space?.layout?.status === NoumLayoutStatus.Unsaved ||
      sectionStatus === NoumLayoutStatus.Unsaved,
    [sectionStatus, space?.layout?.status],
  );

  const hasDraftNoumLayout = useMemo(
    () =>
      space?.layout?.status === NoumLayoutStatus.Draft ||
      sectionStatus === NoumLayoutStatus.Draft,
    [sectionStatus, space?.layout?.status],
  );

  const hasPublishedNoumLayout = useMemo(
    () =>
      space?.layout?.status === NoumLayoutStatus.Published ||
      sectionStatus === NoumLayoutStatus.Published,
    [sectionStatus, space?.layout?.status],
  );

  const hasUnsavedPermissions = useMemo(
    () => SpaceUtils.hasUnsavedPermission(connections),
    [connections],
  );

  const lastCustomPreviewSavedTime = useMemo(
    () => getTimeStampForDisplaying(space?.lastCustomPreviewSavedTime, true),
    [space],
  );

  const publishedDate = useMemo(
    () => getTimeStampForDisplaying(space?.publishedAt, true),
    [space],
  );
  const lastChangedDate = useMemo(
    () => getTimeStampForDisplaying(space?.updatedAt, true),
    [space],
  );
  const hasDraft = useMemo(
    () =>
      SpaceUtils.hasDraftElement(space) || SpaceUtils.hasDraftSetting(space),
    [space],
  );
  const hasPublished = useMemo(
    () => SpaceUtils.hasPublishedElement(space),
    [space],
  );

  const [isNavigateToCustomPreview, setNavigateToCustomPreview] =
    useState(false);

  const { discardSpaceChangeHelper } = useDiscardSpaceChangeHelper();

  const { changeProjectChamberStatusHelper, loading: archiving } =
    useChangeProjectChamberStatusHelper();

  const [unlinkNoumsMutation, { loading: unlinkArchivingLoader }] =
    useUnlinkNoumsMutation({
      onError: (err) => {
        addToast('error', 'icon', err.message);
      },
      onCompleted: () => {
        if (id) refetchSpaceById({ noumId: id });
        addToast('success', 'icon', t('noumena.link_noums.unlink_alert'));
      },
    });

  const handleClose = useCallback(() => {
    toggle(false);
  }, [toggle]);

  const sideBarOptionSelected = (
    value: TNoumEditModal | DropdownValueType<string>,
  ) => {
    switch (value) {
      case 'noumena_copilot':
        setOpenNoumenaCopilot(true);
        break;
      case 'customize':
        setOpenThemePanel(true);
        break;
      case 'manage_members':
        setOpenManageMembers(true);
        break;
      case 'archive':
        setOpenArchive(true);
        break;
      case 'invites':
        setOpenInvites(true);
        break;
      case 'permission':
        setOpenPermission(true);
        break;
      case 'broadcasting':
        setOpenBroadcast(true);
        break;
      case 'custom_preview':
        if (hasUnsaved) setNavigateToCustomPreview(true);
        else navigate(`/noum/${id!}/edit/custom_preview`);
        break;
      case 'noum_ads':
        setOpenNoumAds(true);
        break;
      case 'restore_last_published_version':
        setOpenRestoreModal(true);
        break;
      default:
        break;
    }
  };

  const onRefetchConnections = useCallback(() => {
    refetchConnections();
  }, [refetchConnections]);

  const onApprovedRefetchConnections = useCallback(() => {
    approvedConnectionsRefetch();
  }, [approvedConnectionsRefetch]);

  const onRefetchInvitedConnections = useCallback(() => {
    refetchInvitedConnections();
  }, [refetchInvitedConnections]);

  const onRefetchCampaigns = useCallback(() => {
    refetchCampaigns();
  }, [refetchCampaigns]);

  const onArchive = useCallback(async () => {
    setOpenArchive(false);
    if (space) {
      await changeProjectChamberStatusHelper(
        space._id!,
        SpaceStatusEnum.Archived,
      );
      navigate(-1);
    }
  }, [navigate, space, changeProjectChamberStatusHelper]);

  const unlinkOnArchive = useCallback(async () => {
    setOpenArchive(false);
    if (space?.link?._id && space._id) {
      await unlinkNoumsMutation({
        variables: {
          noumLinkId: space.link?._id,
          linkedNoumIDs: space._id,
        },
      });
    }
  }, [space?._id, space?.link?._id, unlinkNoumsMutation]);

  const handleMarkAsVisited = useCallback(async () => {
    await markSpaceAsEdited({
      variables: { spaceId: id },
      onCompleted: () => {
        setIsVisited(true);
        handleClose();
      },
    });
  }, [markSpaceAsEdited, handleClose, setIsVisited, id]);

  useEffect(() => {
    const discardValues = async () => {
      if (isOnLoad && !loading && !isArchived && isOwner) {
        setIsOnLoad(false);
      }
    };
    discardValues();
  }, [
    discardSpaceChangeHelper,
    data?.getSpaceById,
    hasUnsaved,
    id,
    isOnLoad,
    loading,
    isArchived,
    isOwner,
  ]);

  useEffect(() => {
    if (space && (!space.elements || space.elements.length === 0)) toggle(true);

    setIsVisited(
      !(
        space &&
        (!space.elements || space.elements.length === 0) &&
        !space.updatedAt
      ),
    );
  }, [space]);

  useEffect(() => {
    if (id) {
      setIsOnLoad(true);
    }
  }, [refetchSpaceById, id]);

  return {
    loading,
    publishedDate,
    lastChangedDate,
    hasDraft,
    space,
    hasUnsaved,
    hasUnsavedPermissions,
    isOwner,
    isArchived,
    setOpenArchive,
    openInvites,
    setOpenInvites,
    openPermission,
    setOpenPermission,
    openNoumAds,
    setOpenNoumAds,
    openBroadcast,
    setOpenBroadcast,
    isVisited,
    isOnLoad,
    isOpen,
    hasPublished,
    openArchive,
    archiving,
    handleClose,
    sideBarOptionSelected,
    onArchive,
    handleMarkAsVisited,
    onRefetchCampaigns,
    campaignsLoading,
    campaigns,
    infiniteState,
    fetchMoreCampaigns,
    onRefetchConnections,
    loadingConnections,
    connections,
    connectionsInfiniteState,
    fetchMoreConnections,
    onApprovedRefetchConnections,
    approvedConnectionsLoading,
    approvedConnections: appConnectionsData,
    approvedInfiniteState,
    approvedConnectionsError,
    fetchMoreApprovedConnections,
    isRestored,
    setIsRestored,
    openRestoreModal,
    setOpenRestoreModal,
    linkedNoumsCount,
    links,
    linkedNoumIds,
    unlinkOnArchive,
    unlinkArchivingLoader,
    openThemePanel,
    setOpenThemePanel,
    openManageMembers,
    setOpenManageMembers,
    openNoumenaCopilot,
    setOpenNoumenaCopilot,
    lastCustomPreviewSavedTime,
    isNavigateToCustomPreview,
    setNavigateToCustomPreview,
    isSettingTheme,
    markSpaceAsEditedLoading,
    invitedConnections,
    invitedConnectionsError,
    invitedConnectionsLoading,
    invitedInfiniteState,
    refetchInvitedConnections,
    fetchMoreInvitedConnections,
    onRefetchInvitedConnections,
    refetchSpaceById,
    hasUnsavedNoumLayout,
    hasDraftNoumLayout,
    hasPublishedNoumLayout,
    setsectionStatus,
  };
};

export default useNoumEdit;
