import { t } from 'i18next';
import { throttle } from 'lodash';
import { useEffect, useMemo } from 'react';
import { Icon } from '@/components/Icon';
import {
  IconBackground,
  SingleOptionTextContainer,
  SingleOptionText,
  SingleOptionComingSoonText,
  SingleOptionWrapper,
  ToolTip,
} from './Toolbox.styles';
import { type SingleOptionProps } from './types';

const SingleOption = ({
  name,
  size,
  text,
  isDisabled,
  isComingSoon,
  toolTipText,
  onClick,
}: SingleOptionProps) => {
  const throttledCallback = useMemo(() => throttle(onClick, 3600), [onClick]);

  useEffect(
    () => () => {
      throttledCallback.cancel();
    },
    [throttledCallback],
  );

  return (
    <SingleOptionWrapper
      disabled={isDisabled}
      onClick={throttledCallback}
      data-testid={`singleoptionwrapper-${name}`}
    >
      <IconBackground className="icon-bg">
        <Icon
          name={name}
          size={size}
          color={
            isDisabled || Boolean(isComingSoon)
              ? '--icon-button-neutral-disabled'
              : '--icon-button-neutral-default'
          }
        />
      </IconBackground>
      <SingleOptionTextContainer>
        <SingleOptionText isDisabled={isDisabled || Boolean(isComingSoon)}>
          {text}
        </SingleOptionText>
        {toolTipText && (
          <ToolTip
            font="systemInfo-s"
            colorToken="--text-tooltip-neutral-alt-default"
            top={toolTipText?.length > 60 ? '-30px' : '-20px'}
          >
            {toolTipText}
          </ToolTip>
        )}
        {!!isComingSoon && (
          <SingleOptionComingSoonText
            isDisabled={isDisabled || Boolean(isComingSoon)}
          >
            {t('noumena.comingVerySoon')}
          </SingleOptionComingSoonText>
        )}
      </SingleOptionTextContainer>
    </SingleOptionWrapper>
  );
};

export default SingleOption;
