import { type FC, useCallback, useState } from 'react';
import { t } from 'i18next';
import {
  AddToolbox,
  Toolbox as ToolboxComponent,
} from '@/features/noums/components/Toolbox';
import {
  type CreateElementInput,
  ElementStatusEnum,
  ElementTypeEnum,
  SpaceTypeEnum,
} from '@/apollo/generated/types';
import { Spacer } from '@/layout';
import { ElementUtils } from '@/utils/element';
import { useToast } from '@/hooks';
import { useEditChamberState } from '@/screens/Chamber/EditChamber/provider';
import { useGetWalletQuery } from '@/apollo/graphql';
import {
  type EWalletStatus,
  WALLET_CREATED,
} from '@/screens/Money/Payments/Wallets/types';
import { useAddElementsHelper } from '@/features/noums/hooks/spaceQuery';
import { tabs } from './constants';
import { type ElementCreateToolProps, type ToolboxProps } from './types';

export const Toolbox = (props: ToolboxProps) => {
  const [activeTab, setActiveTab] = useState('xss1');
  const [activeGroup, setActiveGroup] = useState('all');

  const handleChangeCurrentTab = (value: string) => {
    setActiveTab(value);
    tabs.forEach((el) => {
      if (el.id === value) {
        setActiveGroup(el.group);
      }
    });
  };

  return (
    <ToolboxComponent
      tabs={
        props.showFinanceTab
          ? tabs
          : tabs.filter((tab) => tab.group !== 'finance')
      }
      activeTab={activeTab}
      onClick={handleChangeCurrentTab}
      group={activeGroup}
      {...props}
    />
  );
};

export const ElementCreateTool: FC<ElementCreateToolProps> = ({
  position,
  isFirst = false,
  isHidden = false,
  isCustomPreview = false,
}) => {
  const { addToast } = useToast();
  const { data } = useGetWalletQuery();
  const {
    id: spaceId,
    hasMessageElement,
    hasCalendarElement,
    hasQuickQuestionsElement,
    hasPostElement,
    hasWalletElement,
    space,
  } = useEditChamberState();
  const { addElementsHelper, loading } = useAddElementsHelper();
  const [isOpen, setOpen] = useState(false);
  const [showFinanceTab, setShowFinanceTab] = useState(true);

  const handleCloseToolBox = () => {
    setOpen(false);
  };

  const handleOpenToolbox = async () => {
    setOpen(true);
    const walletStatus = data?.getWalletBalance?.status as EWalletStatus;
    if (
      !WALLET_CREATED.includes(walletStatus) ||
      space?.type === SpaceTypeEnum.Home
    ) {
      setShowFinanceTab(false);
    }
  };

  const checkElementExistOrNot = useCallback(
    (elementType: ElementTypeEnum): boolean => {
      let message = '';
      if (elementType === ElementTypeEnum.Message && hasMessageElement) {
        message = t('noumena.chambers.toolbox.message_element_exists');
      } else if (
        elementType === ElementTypeEnum.QuickQuestions &&
        hasQuickQuestionsElement
      ) {
        message = t('noumena.chambers.toolbox.Question_element_exists');
      } else if (
        elementType === ElementTypeEnum.Calendar &&
        hasCalendarElement
      ) {
        message = t('noumena.chambers.toolbox.calendar_element_exists');
      } else if (elementType === ElementTypeEnum.Userposts && hasPostElement) {
        message = t('noumena.chambers.toolbox.post_element_exists');
      } else if (elementType === ElementTypeEnum.Wallet && hasWalletElement) {
        message = t('noumena.chambers.toolbox.subwaleet_element_exists');
      }
      if (message) {
        addToast('error', 'none', message);
      }
      return !!message;
    },
    [
      addToast,
      hasMessageElement,
      hasQuickQuestionsElement,
      hasCalendarElement,
      hasPostElement,
      hasWalletElement,
    ],
  );

  const handleSelectElementType = useCallback(
    async (elementType: ElementTypeEnum) => {
      if (spaceId && !checkElementExistOrNot(elementType)) {
        const newElementInput: CreateElementInput = {
          elementType,
          bodyContentType:
            ElementUtils.getBodyContentTypeFromElementType(elementType),
          status: ElementStatusEnum.Unsaved,
          headerContent:
            elementType === ElementTypeEnum.Wallet
              ? `${space?.name || ''} ${'Wallet'} ` || ''
              : '',
          position,
        };
        await addElementsHelper(spaceId, newElementInput);
        setOpen(false);
      }
    },
    [spaceId, checkElementExistOrNot, space?.name, position, addElementsHelper],
  );

  if (isHidden) return <Spacer height={54} data-testid="spacer" />;

  return isOpen ? (
    <Toolbox
      loading={loading}
      handleCloseToolbox={handleCloseToolBox}
      handleSelectElementType={handleSelectElementType}
      showFinanceTab={showFinanceTab}
    />
  ) : !isCustomPreview ? (
    <AddToolbox isFirst={isFirst} handleOpenToolbox={handleOpenToolbox} />
  ) : (
    <></>
  );
};

export default ElementCreateTool;
