import generate from 'uniqid';
import { t } from 'i18next';
import { memo, useCallback, useState } from 'react';
import { useToast } from '@/hooks';
import { ElementTypeEnum } from '@/apollo/generated/types';
import { NoumWalletCreateModal } from '@/screens/Chamber/components/modals/NoumWalletCreateModal';
import { useEditChamberState } from '@/screens/Chamber/EditChamber/provider';
import { useToolbox } from '@/features/noums/hooks/Toolbox';
import { type ListOfOptionsTypes, type ToolboxContainerProps } from './types';
import SingleOption from './SingleOption';
import { ToolboxOptionsContainer } from './Toolbox.styles';

const ToolboxContainerInner = ({
  group,
  handleSelectElementType,
}: ToolboxContainerProps) => {
  const [showNoumWalletCreateModal, setShowNoumWalletCreateModal] =
    useState<boolean>(false);
  const { addToast } = useToast();
  const { listOfOptions } = useToolbox(group);

  const { hasWalletElement } = useEditChamberState();

  const handleCloseNoumWalletCreateModal = () => {
    setShowNoumWalletCreateModal(false);
  };

  const handleClick = useCallback(
    (element: ListOfOptionsTypes) => {
      if (element.isComingSoon) return;
      if (element.disabled) {
        addToast(
          'primary',
          'icon',
          `${t('noumena.chambers.toolbox.element_exist_first')} ${
            element.text
          } ${t('noumena.chambers.toolbox.element_exist_last')}`,
        );
        return;
      }

      if (handleSelectElementType) {
        if (element.type === ElementTypeEnum.Wallet && hasWalletElement) {
          addToast(
            'error',
            'none',
            t('noumena.chambers.toolbox.subwaleet_element_exists'),
          );
          return;
        }
        if (element.type === ElementTypeEnum.Wallet && !hasWalletElement) {
          setShowNoumWalletCreateModal(true);
        } else {
          handleSelectElementType(element.type);
        }
      }
    },
    [addToast, handleSelectElementType, hasWalletElement],
  );

  return (
    <ToolboxOptionsContainer>
      {listOfOptions.map(
        (el) =>
          el.group.includes(group) && (
            <SingleOption
              key={generate()}
              name={el.name}
              size={el.size}
              text={el.text}
              isDisabled={el.disabled}
              isComingSoon={el.isComingSoon}
              toolTipText={el.toolTipText}
              onClick={() => handleClick(el)}
            />
          ),
      )}
      <NoumWalletCreateModal
        isOpen={showNoumWalletCreateModal}
        handleClose={handleCloseNoumWalletCreateModal}
        handleSelectElementType={handleSelectElementType}
      />
    </ToolboxOptionsContainer>
  );
};

export const ToolboxContainer = memo(ToolboxContainerInner);
