import { useCallback } from 'react';
import * as Sentry from '@sentry/react';
import { useUpdateElementPositionMutation } from '@/apollo/graphql';
import { type ElementPositionInput } from '@/apollo/generated/types';
import { useToast } from '@/hooks/toast';

export function useUpdateElementPositionHelper() {
  const { addToast } = useToast();

  const [updateElementPosition, { loading }] =
    useUpdateElementPositionMutation();

  const updateElementPositionHelper = useCallback(
    async (
      spaceId: string,
      input: ElementPositionInput | ElementPositionInput[],
    ) => {
      let isSuccess;
      try {
        await updateElementPosition({
          variables: { spaceId, input },
        });

        isSuccess = true;
      } catch (error) {
        let message = 'Unknown';
        if (error instanceof Error) {
          message = error.message;
        }
        addToast('error', 'none', message);
        Sentry.captureException(new Error(message), {
          tags: {
            section: 'removeElementMutation',
          },
        });

        isSuccess = false;
      }

      return isSuccess;
    },
    [addToast, updateElementPosition],
  );

  return {
    loading,
    updateElementPositionHelper,
  };
}

export default useUpdateElementPositionHelper;
