import { type FC } from 'react';
import { t } from 'i18next';
import { Icon } from '@/components/Icon';
import { TSpan } from '@/components/Typography';
import { Stack } from '@/layout';
import { useWindowDimensions } from '@/hooks';
import { breakpoints } from '@/constants/devices';
import { HeadTag, HiddenElementWrapper } from './styles';

type HiddenElementsProps = {
  label: string;
};

const HiddenElements: FC<HiddenElementsProps> = ({ label }) => {
  const { width } = useWindowDimensions();
  const isMobile = width < breakpoints.MOBILE_MAX;

  return (
    <HiddenElementWrapper>
      <Stack justify="space-between" align="start" vertical={isMobile}>
        <TSpan
          font="heading-xs-bold"
          colorToken="--text-body-header-neutral-default"
        >
          {label}
        </TSpan>
        <HeadTag isMobile={isMobile}>
          <Icon name="info_m" size={20} color="--icon-tag-neutral" />
          <TSpan font="body-m-bold" colorToken="--text-tag-neutral-default">
            {t('noumena.noum_edit.custom_previews.hidden')}
          </TSpan>
        </HeadTag>
      </Stack>
    </HiddenElementWrapper>
  );
};

export default HiddenElements;
