import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useWindowDimensions } from '@/hooks/dimensions';
import { sizes } from '@/constants/devices';
import { Icon } from '@/components/Icon';
import BasicChipsTabsForm from '@/components/Tabs/TabsForm';
import { Spinner } from '@/components/Spinner';
import {
  TabsFont,
  ToolboxHeader,
  ToolboxHeaderText,
  ToolboxLoader,
  ToolboxTabsCnt,
  ToolboxTitleWrapper,
  ToolboxWrapper,
} from './Toolbox.styles';
import { ToolboxContainer } from './ToolboxContainer';
import { type ToolboxProps } from './types';
import udpateTabs from './helper';

const Toolbox = ({
  tabs,
  onClick,
  activeTab,
  group,
  loading,
  handleCloseToolbox,
  handleSelectElementType,
}: ToolboxProps) => {
  const { t } = useTranslation();
  const [currentTabs, setCurrentTabs] = useState(tabs);
  const { width } = useWindowDimensions();
  const mobileViewWidth = parseInt(sizes.MOBILE_MAX, 10);
  const mobileView = width <= mobileViewWidth;

  useEffect(() => {
    if (mobileView) {
      setCurrentTabs(udpateTabs(tabs, 'medium'));
    } else {
      setCurrentTabs(udpateTabs(tabs));
    }
  }, [mobileView, tabs]);

  return (
    <ToolboxWrapper>
      {loading && (
        <ToolboxLoader>
          <Spinner />
        </ToolboxLoader>
      )}
      <ToolboxHeader>
        <ToolboxTitleWrapper>
          <Icon
            name="close_m"
            size={24}
            color="--icon-button-neutral-default"
            onClick={handleCloseToolbox}
            data-testid="toolboxclose"
          />
          <ToolboxHeaderText>
            {t('noumena.chambers.toolbox.header')}
          </ToolboxHeaderText>
        </ToolboxTitleWrapper>
        <ToolboxTabsCnt isMobile={mobileView}>
          <BasicChipsTabsForm
            inputList={currentTabs}
            isWithoutImage
            onChange={onClick}
            selectedId={activeTab}
            fontSize={TabsFont}
            textFont="--font-body-medium-regular-font"
          />
        </ToolboxTabsCnt>

        <ToolboxContainer
          group={group}
          handleSelectElementType={handleSelectElementType}
        />
      </ToolboxHeader>
    </ToolboxWrapper>
  );
};

export default Toolbox;
