import { memo } from 'react';
import { ElementCreateTool } from '@/screens/Chamber/components/ElementCreate';
import { Element } from '@/screens/Chamber/components/Element';
import { ElementUtils } from '@/utils/element';
import { Spacer } from '@/layout';
import { CustomPreviewTabEnum } from '@/screens/Chamber/CustomPreview/constants';
import { type DragableElementProps } from './types';
import { DNDContainer } from './styles';

export const DragableElement = memo((props: DragableElementProps) => {
  const {
    spaceId,
    element,
    currentIndex,
    totalIndex,
    isDragging,
    provided,
    lift,
    isCustomPreview,
    handleCPVisibilityChange,
    selectedCustomPreviewTab,
  } = props;

  return (
    <DNDContainer
      ref={provided?.innerRef}
      {...provided?.draggableProps}
      isDragging={isDragging}
      data-testid={element._id}
    >
      <ElementCreateTool
        position={ElementUtils.getPosition(element) - 1}
        isHidden={isDragging}
        isCustomPreview={isCustomPreview}
      />
      <Spacer height={16} />
      <Element
        spaceId={spaceId}
        element={element}
        id={`${element._id}`}
        currentTitle={element.headerContent ?? element.elementType ?? 'UNKNOWN'}
        currentIndex={currentIndex}
        totalIndex={totalIndex}
        provided={provided}
        lift={lift}
        isEditing={
          !isCustomPreview ||
          selectedCustomPreviewTab === CustomPreviewTabEnum.Edit
        }
        isCustomPreview={isCustomPreview}
        handleCPVisibilityChange={(isVisible: boolean) =>
          handleCPVisibilityChange?.(element?._id || '', isVisible)
        }
        selectedCustomPreviewTab={selectedCustomPreviewTab}
      />
    </DNDContainer>
  );
});
