import { t } from 'i18next';
import { type FC } from 'react';
import Rearrange from '@/screens/Chamber/components/ElementRearrange/Rearrange';
import { ElementCreateTool } from '@/screens/Chamber/components/ElementCreate';
import { SpaceUtils } from '@/utils/space';
import { Stack } from '@/layout';
import { type EditChamberBodyProps } from './types';
import { AlwaysHiddenTag, Container, Divider } from './styles';
import { useEditChamberState } from './provider';
import HiddenElements from '../CustomPreview/HiddenElements';
import { CustomPreviewTabEnum } from '../CustomPreview/constants';

export const EditChamberBody: FC<EditChamberBodyProps> = (props) => {
  const {
    space,
    elements,
    hasWalletElement,
    hasMessageElement,
    hasFilesManagerElement,
  } = useEditChamberState();
  const { isCustomPreview, selectedCustomPreviewTab } = props;

  return (
    <Container>
      {!elements || (elements.length === 0 && !isCustomPreview) ? (
        <ElementCreateTool position={0} isFirst />
      ) : (
        <>
          <Rearrange {...props} />
          {space && !isCustomPreview && (
            <ElementCreateTool
              position={SpaceUtils.getMaxPosition(space) + 1}
            />
          )}
          {isCustomPreview &&
            selectedCustomPreviewTab === CustomPreviewTabEnum.Edit &&
            (hasMessageElement || hasWalletElement) && (
              <Stack vertical gap={16}>
                <Divider />
                <AlwaysHiddenTag
                  font="body-l-bold"
                  colorToken="--text-body-neutral-default"
                >
                  {t('noumena.noum_edit.custom_previews.always_hidden')}
                </AlwaysHiddenTag>
                {hasWalletElement && (
                  <HiddenElements
                    label={t('noumena.noum_edit.custom_previews.wallet', {
                      noumName: space?.name || '',
                    })}
                  />
                )}
                {hasMessageElement && (
                  <HiddenElements
                    label={t('noumena.noum_edit.custom_previews.massage')}
                  />
                )}
                {hasFilesManagerElement && (
                  <HiddenElements
                    label={t('noumena.noum_edit.custom_previews.files_manager')}
                  />
                )}
              </Stack>
            )}
        </>
      )}
    </Container>
  );
};
