import { type FC } from 'react';
import {
  Droppable,
  type DroppableProvided,
  type DroppableStateSnapshot,
} from 'react-beautiful-dnd';
import { DragableArea } from './DragableArea';
import { DropZone } from './styles';
import { type DroppableAreaProps } from './types';

export const DroppableArea: FC<DroppableAreaProps> = (props) => {
  const { elements } = props;

  return (
    <Droppable droppableId="element_edit_list">
      {(
        dropProvided: DroppableProvided,
        dropSnapshot: DroppableStateSnapshot,
      ) => (
        <DropZone
          ref={dropProvided.innerRef}
          isDraggingOver={dropSnapshot.isDraggingOver}
          isDraggingFrom={Boolean(dropSnapshot.draggingFromThisWith)}
          {...dropProvided.droppableProps}
          data-testid="rearrange-wapper"
        >
          {elements.map((element, index) => (
            <DragableArea
              {...props}
              key={element._id}
              index={index}
              currentIndex={index}
              totalIndex={elements.length - 1}
              element={element}
            />
          ))}
          {dropProvided.placeholder}
        </DropZone>
      )}
    </Droppable>
  );
};
