import { useGetWalletQuery } from '@/apollo/graphql';
import { type EWalletStatus } from '@/screens/Money/Payments/Wallets/types';

export const useCheckWalletStatus = () => {
  const { data } = useGetWalletQuery();

  const walletStatus = data?.getWalletBalance?.status as EWalletStatus;

  return {
    walletStatus,
  };
};
